import React, { useState } from 'react';

import ProjectsListView from './projectsListView';
import ProjectsDetailView from './projectsDetailView';

function Projects(props) {
    const [ projects ] = useState([
        {
            id: 1,
            slug: 'acik-kaynak-icerik',
            title: 'Ak kaynak ierik projesi',
            content: 'Ak kaynak ieriklerinin listelendii proje',
            category: 'Web-Onyuz',
            stars: 55,
            participation: false,
            sponsor: true,
        },
        {
            id: 2,
            slug: 'bibisim',
            title: 'zmir bisiklet projesi',
            content: 'zmir bisiklet duraklar ve srelerini gsteren proje',
            category: 'Web-arkayuz',
            stars: 15,
            participation: false,
            sponsor: true,
        },
    ]);

    if (props.slug !== undefined) {
        const currentProjectsItem = projects.find(x => x.slug === props.slug);

        if (currentProjectsItem !== undefined) {
            return (
                <ProjectsDetailView content={currentProjectsItem} />
            );
        }
    }

    return (
        <ProjectsListView projects={projects} {...props} />
    );
}

export {
    Projects as default,
};
