import React, { useState, useEffect } from 'react';

const bulmaStyles = {};

interface LearnProps {
    contentPath: string;
}

function Learn(props: LearnProps) {
    const [ content, setContent ] = useState(null);

    useEffect(
        () => {
            setContent(`Talep edilen ierik yolu: /${props.contentPath}`);
        },
        [ props.contentPath ],
    );

    return (
        <>
            <h1 className={bulmaStyles.title}>Eitim</h1>

            <p>
                Bu sayfa yapm aamasnda.
                <br />
                <br />
                {content}
            </p>
        </>
    );
}

export {
    Learn as default,
};
